.collection-word {
    position: fixed;
    pointer-events: none;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 20vw;
    color: transparent;
    -webkit-text-stroke-width: 5px;
    -webkit-text-stroke-color: red;
    font-weight: 700;
    z-index: 100;
    line-height: 35rem;
    will-change: transform;

    @include response(tab-port) {
        line-height: 15rem;
    }

    @include response(phone) {
        line-height: 10rem;
    }

    @include no-hover {
        display: none;
    }
}

.collection-title {
    display: none;

    @include no-hover {
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 20vw;
        color: transparent;
        -webkit-text-stroke-width: 5px;
        -webkit-text-stroke-color: red;
        font-weight: 700;
        z-index: 100;
        line-height: 35rem;

        @include response(tab-port) {
            line-height: 15rem;
        }

        @include response(phone) {
            line-height: 10rem;
        }
    }
}


.homepage {
    padding: 10rem 2rem 3rem;

    @include response(tab-land) {
        padding-top: 13rem;
    }

    section {
        display: flex;
        flex-wrap: wrap;
        position: relative;

        &>* {
            pointer-events: none;
        }
    }

    img[class*="__item--1"],
    img[class*="__item--3"] {
        float: right;
    }

    img[class*="__item--2"],
    img[class*="__item--4"] {
        float: left;
    }
}

.collection-item {
    &--center {
        padding: $collection-pd;
        position: relative;
        width: 50%;
    }

    &--small {
        padding: $collection-pd;
        position: relative;
        width: 30%;
    }

    &--big {
        padding: $collection-pd;
        position: relative;
        width: 70%;
    }

    &--full {
        padding: $collection-pd;
        position: relative;
        width: 100%;
    }
}

.collection1 {
    &__item {
        &--1 {
            width: 50%;
            position: absolute;
            right: $collection-pd;
            top: 50%;
            transform: translateY(-50%);
        }

        &--2 {
            width: 100%;
        }

        &--3 {
            width: 70%;
            position: absolute;
            right: $collection-pd;
            top: $collection-pd;
        }

        &--4 {
            width: 90%;
        }
    }
}

